body {
  font-family: 'Raleway', sans-serif;
}

ul {
  text-decoration: none;
  list-style-type: none;
}

ul,
ul li {
  padding: 0;
  margin: 0;
  color: white;
}

a {
  text-decoration: none;
  cursor: pointer;
}

a,
a:hover {
  transition: all 0.4s ease-in-out;
}

* {
  box-sizing: border-box;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
}

.main {
  width: 100%;
}

.limit-width {
  width: 92%;
  margin: 0 auto;
}

@media only screen and (min-width: 420px) {
  .limit-width {
    max-width: 1036px;
  }
}

.footer {
  background-color: #1e1e1e;
  padding: 41px 0;
}

.footer-top li a {
  color: #babfc3;
  font-size: 0.813rem;
  display: block;
  padding: 3px 0;
  margin-bottom: 6px;
}

.footer-copy {
  color: #babfc3;
  font-size: 0.75rem;
}

.footer-bottom {
  padding-top: 34px;
}

.footer-bottom-left {
  display: flex;
  width: 121px;
  justify-content: space-between;
  align-items: center;
}

.footer-top a:hover {
  text-decoration: underline !important;
}

.footer-bottom-right {
  margin-top: 14px;
}

.footer-bottom-right a {
  display: block;
  margin: 4px 0;
}

@media only screen and (min-width: 768px) {
  ul.footer-top {
    display: flex;
    justify-content: space-between;
    max-width: 666px;
  }

  .footer-top li a {
    border-left: 1px solid;
    padding: 0 0 0 11px;
  }

  .footer-top li:first-child a {
    padding-left: 0;
    border-left: none;
  }

  .footer-bottom-right a {
    margin: 0 0 0 20px;
  }

  .footer-bottom-right {
    margin-top: 0;
  }

  .footer-bottom {
    display: flex;
    justify-content: space-between;
  }
  .footer-bottom-right {
    padding-top: 0;
    display: flex;
    justify-content: space-between;
  }
}

.header {
  height: 3.75rem;
  display: flex;
  align-items: center;
  background-color: #424242;
}

.header h2 {
  line-height: 0;
  color: white;
}

.navbar {
  height: 3.75rem;
  background: #0098ff; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    #0098ff 0%,
    #006aff 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0098ff', endColorstr='#006aff',GradientType=0 ); /* IE6-9 */
}

.navbar .frame {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: 0 auto;
}

.navbar .frame .navbar-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar .frame h1 {
  padding: 0;
  margin: 0;
  color: white;
  font-size: 0.9rem;
  font-weight: 600;
}

.navbar .frame .navbar-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.825rem;
}

.navbar .frame .navbar-btn-login {
  border: none;
  color: white;
  background-color: transparent;
}

.navbar .frame .navbar-btn-cta {
  color: white;
  border: none;
  background-color: #424242;
  margin-left: 10px;
  padding: 4px 5px;
}

@media only screen and (min-width: 768px) {
  .navbar .frame h1 {
    font-size: 2rem;
  }

  .navbar .frame .navbar-btn-cta {
    width: 150px;
    height: 40px;
    margin-left: 20px;
  }

}

.filter-form {
  margin-top: 10px;
  height: 37px;
  position: relative;
}

.filter-form img {
  position: absolute;
  right: 17px;
  top: 15px;
}

.filter-form select {
  font-size: 13px;
  color: #b4b4b4;
  border: #b6b6b6;
  box-shadow: 0px 3px 10px 0px rgba(182, 182, 182, 1);
  height: 100%;
  width: 100%;
  padding-left: 5px;
}

@media only screen and (min-width: 420px) {
  .filter-form {
    width: 286px;
  }
}

.search-form {
  display: flex;
  height: 37px;
  margin-top: 10px;
  width: 100%;
}

.search-form button {
  background-color: #017efe;
  border: none;
  width: 64px;
  height: 100%;
}

.search-form input {
  height: 100%;
  border: #b6b6b6;
  box-shadow: 0px 3px 10px 0px rgba(182, 182, 182, 1);
  width: calc(100% - 64px);
}

.search-form input {
  font-size: 13px;
  color: #b4b4b4;
}

.search-form button img {
  transform: translateY(2px);
}

@media only screen and (min-width: 420px) {
  .search-form {
    width: 433px;
  }
}

.input-bar {
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 420px) {
  .input-bar {
    flex-direction: row;
    justify-content: space-between;
    margin-top: 22px;
    margin-bottom: 1px;
  }
}

.thumbnail {
  display: block;
  margin-top: 1.938rem;
  width: 8.563rem;
  margin-right: 0.688rem;
}

.thumbnail .frame {
  height: 201px;
  background: #1e1e1e;
  position: relative;
}

.thumbnail .frame img {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.thumbnail .frame h3 {
  position: absolute;
  z-index: 2;
  margin-top: 0;
  color: #fdfdfd;
  text-transform: uppercase;
  font-size: 1.688rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.thumbnail-p {
  font-size: 0.813rem;
  color: #1e1e1e;
  padding: 9px 0 0 1px;
  line-height: 0;
}

.card {
  display: block;
  margin-top: 1.938rem;
  width: 8.563rem;
}

.card img {
  width: 100%;
}

.card-p {
  font-size: 0.813rem;
  color: #1e1e1e;
  padding: 9px 0 0 1px;
  line-height: 0;
}

.error-text {
  line-height: 0;
  font-size: 1rem;
  color: #414141;
}

.loading {
  padding-top: 2rem;
}

.loading p {
  line-height: 0;
  font-size: 1rem;
  color: #414141;
}

.pagination-container {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.pagination button {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: #fff;
  border: 1px solid #a0aec0;
  font-size: 20px;
  padding: 5px 10px;
  cursor: pointer;
}

.pagination button:disabled {
  color: #cbd5e0;
}

.active {
  font-weight: bold;
  background: #e2e8f0;
}

@media only screen and (min-width: 420px) {
  .pagination-container {
    margin-bottom: 20px;
  }
}

.movie-list {
  min-height: calc(100vh - 337px);
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-bottom: 62px;
}

@media only screen and (min-width: 420px) {
  .movie-list {
    justify-content: space-between;
  }
}

.series-list {
  min-height: calc(100vh - 337px);
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-bottom: 62px;
}

@media only screen and (min-width: 420px) {
  .series-list {
    justify-content: space-between;
  }
}

.error {
  min-height: calc(100vh - 337px);
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding-bottom: 62px;
  padding-top: 2rem;
}

